@import url(https://fonts.googleapis.com/css2?family=Archivo:wght@500&display=swap);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
        'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Helvetica, Arial, sans-serif,
        'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
    font-size: 14px;
    line-height: 1.5;
    color: #24292e;
    background-color: #fff;
}

.app {
    flex-direction: column;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

input.error {
    border-color: red;
}

.input-feedback {
    color: red;
    height: 5px;
    margin-top: -12px;
}

.menu {
    padding: 0 20px;
    border-bottom: solid 1px #e8e8e8;
    box-shadow: 0 0 30px #f3f1f1;
    background-color: white;
}

.menu__logo {
    width: 150px;
    float: left;
}

.menu__logo a {
    display: inline-block;
    font-size: 20px;
    padding: 19px 20px;
}

.menu__container .ant-menu-item {
    padding: 0px 5px;
}

.menu__container .ant-menu-submenu-title {
    padding: 10px 20px;
}

.menu__container .ant-menu-item a,
.menu__container .ant-menu-submenu-title a {
    padding: 10px 15px;
}

.menu__container .ant-menu-horizontal {
    border-bottom: none;
}

.menu__container .menu_left {
    float: left;
}

.menu__container .menu_rigth {
    float: right;
}

.menu__mobile-button {
    float: right;
    height: 32px;
    padding: 6px;
    margin-top: 8px;
    display: none !important; /* use of important to overwrite ant-btn */
    background: #3e91f7;
}

.menu_drawer .ant-drawer-body {
    padding: 0 !important;
}

/* align header of Drawer with header of page */
.menu_drawer .ant-drawer-header {
    padding: 14px 24px !important;
}

@media (max-width: 767px) {
    .menu__mobile-button {
        display: inline-block !important;
    }

    .menu_left,
    .menu_rigth {
        display: none;
    }

    .menu__logo a {
        margin-left: -20px;
    }

    .menu__container .ant-menu-item,
    .menu__container .ant-menu-submenu-title {
        padding: 1px 20px;
    }

    .menu__logo a {
        padding: 10px 20px;
    }
}

/* Common Style */
.body__container {
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    font-weight: 400;
    color: #333;
}
a {
    text-decoration: none; /* 텍스트에 줄을 그어주는 속성, a태그의 밑줄을 없애는데 자주 사용 */
}

/* Section $ Inner */
.sections {
    position: relative;
}
.sections .inner {
    max-width: 1200px;
    margin: 0 auto;
    box-sizing: border-box; /* padding or border에 의해 무분별하게 요소가 커지는 것을 방지함 */
    position: relative;
}

/* Float Clearfix Style */
.clearfix::after {
    content: '';
    clear: both;
    display: block;
}
.float--left {
    float: left;
}
.float--right {
    float: right;
}

/* Footer Style */
footer .inner {
    padding: 30px 0;
    border-top: 3px solid #eee;
}

footer .site-links {
    display: flex;
}

footer .site-links li {
    font-size: 14px;
    margin-right: 16px;
    color: #767676;
}

footer .site-links li a {
    color: #4078c0;
}

footer .site-links li a:hover {
    text-decoration: underline;
}

footer .logo {
    position: absolute;
    /* absolute를 기준으로 전체영역을 다 사용한다는 개념 */
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto; /* 4방향에 대한 마진을 auto로 설정함으로써 가운데 정렬 */
    width: 32px;
    height: 32px;
}

footer .logo:hover svg {
    /* logo요소에 마우스를 올리면 svg 속성은 아래와 같이 설정하겠다는 의미 */
    fill: #4078c0;
}

/* Medium responsive (width < 1024px) */
@media screen and (max-width: 1024px) {
    footer .inner {
        padding: 50px 20px;
    }
}

@media screen and (max-width: 1024px) {
    footer .logo {
        display: none;
    }
}

/* Small responsive (width < 768px) */
@media screen and (max-width: 768px) {
    footer .site-links {
        float: none;
        display: block;
        text-align: center;
    }
}

@media screen and (max-width: 768px) {
    footer .site-links:first-child {
        margin-bottom: 20px;
    }
}

@media screen and (max-width: 768px) {
    footer .site-links li {
        display: inline;
        /* 화면에 출력하는 방식을 글자를 다루는데 특화함
        * 즉 각각의 a태그가 텍스트로 인지된다고 볼수있음 */
    }
}

table {
    font-family: arial, sans-serif;
    border-collapse: collapse;
    width: 100%;
}

td,
th {
    border: 1px solid #dddddd;
    text-align: center;
    padding: 8px;
}

tr:nth-child(even) {
    background-color: ghostwhite;
}

.Mapdiv {
    margin: '5rem 10rem 0 10rem';
    padding-bottom: '10rem';
    max-width: 800px;
    font-family: 'Archivo', sans-serif;
    margin: 4rem auto 100px;
}

.LandingBody {
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    font-weight: 400;
    color: #333;
}

/* Float Clearfix Style */
.clearfix::after {
    content: '';
    clear: both;
    display: block;
}
.float--left {
    float: left;
}
.float--right {
    float: right;
}

/* Section $ Inner */
.section {
    position: relative;
}
.section .inner {
    max-width: 980px;
    margin: 0 auto;
    box-sizing: border-box; /* padding or border에 의해 무분별하게 요소가 커지는 것을 방지함 */
    position: relative;
}

/* Button Style */
.btn {
    height: 30px;
    background: #eee linear-gradient(to bottom, #fcfcfc, #eee);
    border: 1px solid #d5d5d5;
    border-radius: 4px;
    display: flex; /* inline 특성을 가진 박스요소 */
    align-items: center;
    padding: 0 12px;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.5;
    cursor: pointer;
    box-sizing: border-box;
    position: relative;
    color: #333;
}
.btn:hover::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.07);
}
.btn.btn--primary {
    border: 1px solid #65b836;
    color: #fff;
    background: #55a532 linear-gradient(#91dd70, #55ae2e);
    margin-top: 2rem;
}

/* list style reset */
ol,
ul {
    list-style: none;
}

/* Input-text Style */
.input--text {
    height: 34px;
    padding: 0 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
    box-sizing: border-box;
    outline: none;
    box-shadow: inset 0 2px 2px rgba(0, 0, 0, 0.075);
    font-size: 16px;
    margin-top: 1rem;
}
.input--text:focus {
    border-color: #51a7e8;
    box-shadow: inset 0 2px 2px rgba(0, 0, 0, 0.075), 0 0 5px rgba(81, 167, 232, 0.5);
    /* 가상요소선택자를 통해 다중으로 스타일을 부여 할 수 있음(","를 통해 구분해줌) */
}
/* Vender Prefix : 브라우저 업체별 접두사
   * 정식 자바스크립트 문법은 아니지만 접두사를 붙여 브라우져별로 스타일 적용이 가능함 */
.input--text::-webkit-input-placeholder {
    color: #cacaca;
} /*크롬*/
.input--text::-ms-input-placeholder {
    color: #cacaca;
} /*마이크로소트트사*/
.input--text::-moz-input-placeholder {
    color: #cacaca;
} /*파이어폭스*/

/* Visual Style */
.section--visual {
    background-image: url(/Online-ShoppingMall/static/media/landingPageImg.b28e3e30.jpg);
    background-repeat: no-repeat; /* 배경이미지의 중복배치를 막음*/
    background-position: bottom left;
    background-size: cover;
}

.section--visual::before {
    content: '';
    position: absolute;
    /* position을 absolute로 선언하기 위해서는 부소요소의 position이 반드시 정의되어야 함
     * 실제로 section--visual의 경우 common style에 .section으로 정의되어있음 */

    top: 0;
    left: 0;
    bottom: 0;
    right: 0; /* 4방향으로 크기를 0으로주고 부모요소를 기준으로 배치를하게 되면 세로/가로 100%와 같음 */
    background: rgba(8, 8, 8, 0.7);
}

/* inner 요소 */
.section--visual .inner {
    padding: 140px 0;
    display: flex; /* 내부의 요소를 수평정렬 하기위해서 flex로 선언 */
}

.section--visual .summary {
    flex-grow: 1;
    flex-basis: 0; /* flex-basis default값은 auto임 */
    margin-right: 90px;
}

.section--visual .summary__title {
    color: #fff;
    font-size: 54px;
    text-shadow: 0 1px 5px rgba(255, 255, 255, 0.15), 0 1px 25px rgba(255, 255, 255, 0.5);
}

.section--visual .summary__description {
    color: #fff;
    text-shadow: 0 1px 5px rgba(255, 255, 255, 0.15), 0 1px 25px rgba(241, 236, 236, 0.75);
}

#sign-form {
    width: 340px;
    margin-top: 16px;
}

#sign-form li {
    margin-bottom: 17px;
}

#sign-form li:last-child {
    /* 가상 요소 선택자 : 마지막 요소는 굳이 bottom에 대한 margin값을 부여할 필요가 없음 */
    margin-bottom: 0px;
}

#sign-form .input--text {
    width: 100%;
    height: 40px;
}

#sign-form .caption {
    font-size: 12px;
    margin-top: 5px;
    color: rgba(255, 255, 255, 0.6);
    line-height: 1.5;
    text-align: center;
}

#sign-form [type='submit'] {
    /* form 태그에는 하나의 submit만 올수 있으므로 고유한 특성과 같음 */
    width: 100%;
    height: 62px;
    padding: 0 25px;
    font-size: 20px;
    text-align: center;
    justify-content: center;
}

/* Feature Style */
.section--feature {
    background: #f5f5f5;
    padding-top: 66px;
}
.section--feature .summary {
    max-width: 820px;
    margin: 0 auto; /* 반드시 가로너비가 정의되어 있어야함 */
    text-align: center;
}
.section--feature .video {
    max-width: 650px;
    margin: 50px auto;
}

.section--feature .video .video-ratio {
    height: 0;
    padding-top: 56.25%;
    position: relative;
}

.section--feature .video .video-ratio iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.section--feature .tiles {
    border-top: 2px solid #e5e5e5;
    border-bottom: 2px solid #e5e5e5;
}

.section--feature .tiles .inner {
    max-width: 1200px;
}
.section--feature .tiles ul {
    /* 해당 grid 분할은 고버전의 브라우져에서만 가능하므로 수정되어야 함
     * display: grid;
     * grid-template-columns: repeat(4, 1fr);  1:1 비율로 4개의 칼럼 생성 */
}
.section--feature .tiles li {
    padding: 34px 24px;
    text-align: center;
    line-height: 1.5;
    border-right: 1px solid #e5e5e5;
    box-sizing: border-box;
    float: left; /* float 속성을 부여한 경우 부모에는 반드시 clearfix 속성을 부여해줘야 함 */
    width: 25%; /* %로 치수를 부여하는 경우 반드시 box-sizing: border-box로 정의해줘야 함 */
}

.section--feature .tiles li:last-child {
    border-right: none;
}

.section--feature .tiles li img {
    max-width: 100%;
    padding: 14px 10% 24px;
    box-sizing: border-box; /* padding에 의해 요소가 line 밖으로 커지는걸 막아줌 */
}

.section--feature .tiles li h3 {
    font-size: 14px;
    font-weight: 500;
    margin-bottom: 10px;
}
.section--feature .tiles li p {
    font-size: 14px;
    color: #767676;
}

.legend {
    list-style: none;
    margin-left: 10px;
}
.legend li {
    float: left;
    margin-right: 15px;
}
.legend span {
    border: 1px solid #ccc;
    float: left;
    width: 10px;
    height: 12px;
    margin: 2px;
}
.legend {
    position: fixed;
    top: 62%;
    left: 10%;
    /* bring your own prefixes */
    transform: translate(-50%, -50%);
}
/* your colors */
.legend .greendot {
    background-color: #00ff00;
}
.legend .yellowdot {
    background-color: #ffff33;
}
.legend .reddot {
    background-color: #ff3333;
}
.legend .blackdot {
    background-color: #000000;
}

